import { useState } from 'react';
import { Box, Paper, IconButton, Tooltip } from '@mui/material';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { useCopyToClipboard } from '../../hooks/useCopyToClipboard';

/**
 * Component hin th token vi ty chn hin th/n v sao chp
 * 
 * @param {Object} props - Properties
 * @param {string} props.token - Token cn hin th
 * @param {boolean} props.initialVisibility - Trng thi hin th ban u (mc nh: false)
 * @param {function} props.onCopy - Callback khi sao chp (ty chn)
 */
const TokenDisplay = ({ 
  token, 
  initialVisibility = false,
  onCopy,
  sx = {}
}) => {
  const [showToken, setShowToken] = useState(initialVisibility);
  const { copyToClipboard, copied } = useCopyToClipboard();

  const handleCopy = () => {
    if (!token) return;
    
    copyToClipboard(token);
    if (onCopy) onCopy(token);
  };

  return (
    <Box position="relative" mb={2} sx={{ width: '100%', ...sx }}>
      <Paper
        variant="outlined"
        sx={{
          p: 2,
          backgroundColor: 'background.default',
          maxHeight: 100,
          overflow: 'auto',
          fontFamily: 'monospace',
          fontSize: '0.85rem',
          wordBreak: 'break-all',
          pr: 5 
        }}
      >
        {showToken ? token : ''}
      </Paper>
      <Box sx={{ position: 'absolute', top: 8, right: 8, display: 'flex' }}>
        <Tooltip title={showToken ? "Hide token" : "Show token"}>
          <IconButton
            size="small"
            onClick={() => setShowToken(!showToken)}
            sx={{ mr: 1 }}
          >
            {showToken ? <VisibilityOffIcon fontSize="small" /> : <VisibilityIcon fontSize="small" />}
          </IconButton>
        </Tooltip>
        <Tooltip title={copied ? "Copied!" : "Copy"}>
          <IconButton
            size="small"
            onClick={handleCopy}
          >
            {copied ? <CheckCircleIcon fontSize="small" color="success" /> : <ContentCopyIcon fontSize="small" />}
          </IconButton>
        </Tooltip>
      </Box>
    </Box>
  );
};

export default TokenDisplay;